	module.exports = {"data":{"markdownRemark":{"html":"<h1>Contributing to SIG Apps</h1>\n<p>Welcome to contributing to SIG Apps.</p>\n<p>SIG Apps has multiple areas you can contribute to. Those contributions can be in\nthe form of code, documentation, support, being involved in mailing list\ndiscussions, attending meetings, and more. This guide describes different\nmajor functional areas SIG Apps is involved in, provides an overview of the\nareas, and gives pointers on getting more involved in each area. Consider this\na launching point or the start of a <a href=\"https://en.wikipedia.org/wiki/Choose_Your_Own_Adventure\">choose your own adventure</a>\nfor SIG Apps.</p>\n<h2>Major Areas</h2>\n<p>The major launching off point for SIG Apps comes in the form of the different\nareas we're involved in. Each of the areas has a different scope, a slightly\ndifferent feel to the active developers, and small differences to the way\nthings are done.</p>\n<p>To get an idea of what's going on, here's an overview of each of the areas.</p>\n<h3>Workloads API</h3>\n<p>The <em>Workloads API</em> including controllers and jobs is part of SIG Apps.The\ncontrollers are Deployments, DaemonSets, StatefulSets, and ReplicaSets. The jobs\nare part of the batch API and include Jobs and CronJobs. These are part of core\nKubernetes and developed within the <a href=\"https://github.com/kubernetes/kubernetes\">monorepo</a>.</p>\n<p>Don't let the fact that these are part of core Kubernetes intimidate you. Each\ncontroller is its own package that can be found in the <a href=\"https://github.com/kubernetes/kubernetes/tree/master/pkg/controller\">pkg/controllers directory</a>. Each\ncontroller is small.</p>\n<p>Like other parts of core Kubernetes, you can learn about contributing code or\nreviewing pull requests via the <a href=\"https://github.com/kubernetes/community/tree/master/contributors/guide\">Contributors Guide</a>.</p>\n<h3>Helm</h3>\n<p><a href=\"https://helm.sh\">Helm</a> is the package manager for Kubernetes. The packages for\nHelm are called Charts. Helm version 2 is the current stable release series and\nis currently focused on stability, minor non-breaking feature additions, and\nbetter documentation.</p>\n<p>Helm is a sub-project on a much smaller scale from Kubernetes core, the most\nactive repository on GitHub. Contributing to Helm and navigating the project is\nsimilar to many other open source projects.</p>\n<p>The <a href=\"https://github.com/kubernetes/helm/blob/master/CONTRIBUTING.md\">Helm Contributing Guide</a>\ncontains much of what you need to get started including:</p>\n<ul>\n<li>Support and conversation channels</li>\n<li>Filing issues and the lifecycle of issues</li>\n<li>Project milestones and what <a href=\"http://semver.org\">Semantic Versioning</a> means to Helm</li>\n<li>Details on contributing a patch, via a pull request, and what to expect</li>\n</ul>\n<p>The Helm maintainers have a weekly public meeting that's open to anyone to attend.\nThe meeting is recorded and available on YouTube for those unable to attend.\nDetails on the meeting are in the <a href=\"README.md\">Readme</a> for SIG Apps.</p>\n<p>In addition to Helm itself, there are a number of sub-projects of Helm. These\nprojects can be found on the <a href=\"https://github.com/kubernetes-helm\">kubernetes-helm</a>.\nEach of these projects has its own contribution guide but is part of the broader\nHelm project.</p>\n<h3>Community Charts</h3>\n<p><a href=\"https://github.com/kubernetes/charts\">Charts</a> is a community curated set of Helm\npackages for Kubernetes. While organizations and individuals are encouraged to\ncreate their own charts – something Helm provides tools for – the community\ncharts are a place for people to use, share, and contribute to.</p>\n<p>There are a few ways to contribute to charts:</p>\n<ol>\n<li>Create or improve charts</li>\n<li>Contribute to continuous testing</li>\n<li>Write down best practices or document other details</li>\n</ol>\n<p>Charts has its own <a href=\"https://github.com/kubernetes/charts/blob/master/CONTRIBUTING.md\">contributing guide</a>\nand <a href=\"https://github.com/kubernetes/charts/blob/master/REVIEW_GUIDELINES.md\">review guidelines</a>\nthat can act as a launching off point for involvement.</p>\n<p>The charts maintainers have a weekly meeting in addition to the normal SIG Apps\nmeeting. Details on the meeting are in the <a href=\"README.md\">Readme</a> for SIG Apps. </p>\n<h3>Kompose</h3>\n<p><a href=\"https://github.com/kubernetes/kompose\">Kompose</a> stands for Kubernetes + Compose\n(the docker tooling). This project translates Docker compose configuration into\nKubernetes configuration.</p>\n<p>Kompose, like Helm and the community charts, has its own <a href=\"https://github.com/kubernetes/kompose/blob/master/CONTRIBUTING.md\">contributing guide</a>.</p>\n<h2>Contributing Beyond Code</h2>\n<p>There are a couple ways to contribute beyond code and the sub-projects. They include:</p>\n<ul>\n<li>In almost all SIG Apps meetings there are <strong>demos</strong>. Those can include tools to\nhelp with running applications, new ways of building workloads, methods for\ntying different tools together, and more. If you are interested in\ncontributing a demo please contact the SIG leads via the\n<a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-apps\">mailing list</a></li>\n<li>The <strong>App Def Working Group</strong> is a cross collaboration with SIG CLI, SIG API\nMachinery, and others to look at how we can improve the core tooling and\nprocess for operating applications. To produce real world solutions we want\ninput from people who operate applications. If you are interested in learning\nmore please contact the App Def Working Group mailing list or attend a\nmeeting. You can find out more on the\n<a href=\"https://github.com/kubernetes/community/tree/master/wg-app-def\">working groups organization page</a></li>\n</ul>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-apps/contributing/"}}

